import React, { useState, useEffect, useRef, useCallback } from 'react';
import './App.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { 
  faMagnifyingGlassMinus,
  faMagnifyingGlassPlus,
  faWandMagicSparkles,
  faArrowsToCircle, 
  faCheckDouble, 
  faLayerGroup,
  faCircleInfo,
  faSliders,
  faX,
  faSpinner
} from '@fortawesome/free-solid-svg-icons';
library.add(fas);

// PNG paths with both low and high res versions
const IMAGE_PATHS = [
  { 
    lowRes: 'imgs/Background map_low.png',    
    highRes: 'imgs/Background map_high.png',
    name: 'Background Map'
  },
  { 
    lowRes: 'imgs/Case 1_low.png',    
    highRes: 'imgs/Case 1_high.png',
    name: 'Case 1'
  },
  { 
    lowRes: 'imgs/Case 2_low.png',    
    highRes: 'imgs/Case 2_high.png',
    name: 'Case 2'
  },
  { 
    lowRes: 'imgs/Case 3_low.png',    
    highRes: 'imgs/Case 3_high.png',
    name: 'Case 3'
  },
  { 
    lowRes: 'imgs/Case 4_low.png',    
    highRes: 'imgs/Case 4_high.png',
    name: 'Case 4'
  },
  { 
    lowRes: 'imgs/Case 5_low.png',    
    highRes: 'imgs/Case 5_high.png',
    name: 'Case 5'
  },
  { 
    lowRes: 'imgs/Case 6_low.png',    
    highRes: 'imgs/Case 6_high.png',
    name: 'Case 6'
  },
  { 
    lowRes: 'imgs/Case 7_low.png',    
    highRes: 'imgs/Case 7_high.png',
    name: 'Case 7'
  },
  { 
    lowRes: 'imgs/Case 8_low.png',    
    highRes: 'imgs/Case 8_high.png',
    name: 'Case 8'
  }
];

// Threshold for switching to high-res
const HIGH_RES_THRESHOLD = 1.5;

function App() {
  const [layers, setLayers] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  
  // Zoom and pan state
  const [scale, setScale] = useState(1);
  const [position, setPosition] = useState({ x: 0, y: 0 });
  const [containerSize, setContainerSize] = useState({ width: 0, height: 0 });
  
  // Track which layers need high-res
  const [highResLayers, setHighResLayers] = useState(new Set());
  
  // Simple modal state
  const [showModal, setShowModal] = useState(false);
  const [modalText, setModalText] = useState('');
  
  const containerRef = useRef(null);
  const isDragging = useRef(false);
  const dragStart = useRef({ x: 0, y: 0 });
  const lastPosition = useRef({ x: 0, y: 0 });
  const initialPositionSet = useRef(false);
  const previousScale = useRef(1);

  // Simple modal helper
  const showResolutionModal = (text, duration = 1500) => {
    setModalText(text);
    setShowModal(true);
    
    // Auto-hide after duration
    setTimeout(() => {
      setShowModal(false);
    }, duration);
  };

  // Get container size
  useEffect(() => {
    const updateContainerSize = () => {
      if (containerRef.current) {
        const rect = containerRef.current.getBoundingClientRect();
        setContainerSize({ width: rect.width, height: rect.height });
      }
    };

    updateContainerSize();
    window.addEventListener('resize', updateContainerSize);
    return () => window.removeEventListener('resize', updateContainerSize);
  }, []);

  // Set initial position only once when layers are first loaded
  useEffect(() => {
    if (layers.length > 0 && layers[0].naturalSize && !initialPositionSet.current && containerRef.current) {
      const rect = containerRef.current.getBoundingClientRect();
      const firstLayer = layers[0];
      
      // Calculate center position for both x and y axes
      const centerX = (rect.width - (firstLayer.naturalSize.width * scale)) / 10;
      const centerY = (rect.height - (firstLayer.naturalSize.height * scale)) / 200;
      
      setPosition({ x: centerX, y: centerY });
      initialPositionSet.current = true; // Mark as set
    }
  }, [layers, scale]); // Only run when layers or scale change

  // Load low-res images on mount
  useEffect(() => {
    loadLowResImages();
  }, []);

  const loadLowResImages = async () => {
    setIsLoading(true);
    try {
      const loadedLayers = await Promise.all(
        IMAGE_PATHS.map(async (imageConfig, index) => {
          try {
            const img = new Image();
            await new Promise((resolve, reject) => {
              img.onload = resolve;
              img.onerror = () => reject(new Error(`Failed to load ${imageConfig.lowRes}`));
              img.src = imageConfig.lowRes;
            });
            
            return {
              id: index + 1,
              name: imageConfig.name,
              lowResPath: imageConfig.lowRes,
              highResPath: imageConfig.highRes,
              opacity: 1,
              visible: index === 0,
              lowResImage: img,
              highResImage: null,
              isHighResLoaded: false,
              error: null,
              naturalSize: {
                width: img.naturalWidth,
                height: img.naturalHeight
              }
            };
          } catch (error) {
            console.error(`Error loading image ${imageConfig.lowRes}:`, error);
            return {
              id: index + 1,
              name: imageConfig.name,
              lowResPath: imageConfig.lowRes,
              highResPath: imageConfig.highRes,
              opacity: 1,
              visible: index === 0,
              lowResImage: null,
              highResImage: null,
              isHighResLoaded: false,
              error: error.message,
              naturalSize: { width: 0, height: 0 }
            };
          }
        })
      );
      setLayers(loadedLayers);
    } catch (error) {
      console.error('Error loading images:', error);
    } finally {
      setIsLoading(false);
    }
  };

  // Enhanced zoom handlers with modal
  const handleZoomIn = useCallback(() => {
    const newScale = Math.min(10, scale * 1.5);
    
    // Check for threshold crossing
    if (scale < HIGH_RES_THRESHOLD && newScale >= HIGH_RES_THRESHOLD) {
      showResolutionModal('Loading High Resolution...');
    }
    
    if (containerRef.current) {
      const rect = containerRef.current.getBoundingClientRect();
      const centerX = rect.width / 2;
      const centerY = rect.height / 2;
      
      const scaleFactor = newScale / scale;
      const newX = position.x - (centerX - position.x) * (scaleFactor - 1);
      const newY = position.y - (centerY - position.y) * (scaleFactor - 1);
      
      setScale(newScale);
      setPosition({ x: newX, y: newY });
    } else {
      setScale(newScale);
    }
    previousScale.current = newScale;
  }, [scale, position]);

  // Center-based zoom out
  const handleZoomOut = useCallback(() => {
    const newScale = Math.max(0.1, scale * 0.67);
    
    // Check for threshold crossing
    if (scale >= HIGH_RES_THRESHOLD && newScale < HIGH_RES_THRESHOLD) {
      showResolutionModal('Switching to Low Resolution...');
    }
    
    if (containerRef.current) {
      const rect = containerRef.current.getBoundingClientRect();
      const centerX = rect.width / 2;
      const centerY = rect.height / 2;
      
      const scaleFactor = newScale / scale;
      const newX = position.x - (centerX - position.x) * (scaleFactor - 1);
      const newY = position.y - (centerY - position.y) * (scaleFactor - 1);
      
      setScale(newScale);
      setPosition({ x: newX, y: newY });
    } else {
      setScale(newScale);
    }
    previousScale.current = newScale;
  }, [scale, position]);

  // Enhanced wheel handler with modal
  const handleWheel = useCallback((e) => {
    e.preventDefault();

    const zoomIntensity = 0.001;
    const wheel = -e.deltaY * zoomIntensity;
    const newScale = Math.max(0.1, Math.min(10, scale * Math.exp(wheel)));

    // Check for resolution threshold crossing
    const wasBelowThreshold = previousScale.current < HIGH_RES_THRESHOLD;
    const isNowAboveThreshold = newScale >= HIGH_RES_THRESHOLD;
    const wasAboveThreshold = previousScale.current >= HIGH_RES_THRESHOLD;
    const isNowBelowThreshold = newScale < HIGH_RES_THRESHOLD;

    if (wasBelowThreshold && isNowAboveThreshold) {
      showResolutionModal('Loading High Resolution...');
    } else if (wasAboveThreshold && isNowBelowThreshold) {
      showResolutionModal('Switching to Low Resolution...');
    }

    const rect = containerRef.current.getBoundingClientRect();
    const mouseX = e.clientX - rect.left;
    const mouseY = e.clientY - rect.top;

    const scaleFactor = newScale / scale;
    const newX = position.x - (mouseX - position.x) * (scaleFactor - 1);
    const newY = position.y - (mouseY - position.y) * (scaleFactor - 1);

    setScale(newScale);
    setPosition({ x: newX, y: newY });
    previousScale.current = newScale;
  }, [scale, position]);

  // Load high-res for specific layer
  const loadHighResImage = async (layerId) => {
    const layer = layers.find(l => l.id === layerId);
    if (!layer || layer.isHighResLoaded || layer.error) return;

    try {
      console.log(`Loading high-res for ${layer.name}`);
      const img = new Image();
      await new Promise((resolve, reject) => {
        img.onload = resolve;
        img.onerror = () => reject(new Error(`Failed to load ${layer.highResPath}`));
        img.src = layer.highResPath;
      });

      setLayers(prev => prev.map(l =>
        l.id === layerId ? {
          ...l,
          highResImage: img,
          isHighResLoaded: true,
          error: null
        } : l
      ));

      setHighResLayers(prev => new Set([...prev, layerId]));
    } catch (error) {
      console.error(`Error loading high-res image ${layer.highResPath}:`, error);
      setLayers(prev => prev.map(l =>
        l.id === layerId ? { ...l, error: error.message } : l
      ));
    }
  };

  // Unload high-res for specific layer (free memory)
  const unloadHighResImage = (layerId) => {
    setLayers(prev => prev.map(l =>
      l.id === layerId ? {
        ...l,
        highResImage: null,
        isHighResLoaded: false
      } : l
    ));

    setHighResLayers(prev => {
      const newSet = new Set(prev);
      newSet.delete(layerId);
      return newSet;
    });
  };

  // Smart resolution management based on zoom level
  useEffect(() => {
    if (scale >= HIGH_RES_THRESHOLD) {
      // Load high-res for visible layers when zoomed in
      layers.forEach(layer => {
        if (layer.visible && !layer.isHighResLoaded && !layer.error) {
          loadHighResImage(layer.id);
        }
      });
    } else {
      // Unload high-res when zoomed out to save memory
      highResLayers.forEach(layerId => {
        unloadHighResImage(layerId);
      });
    }
  }, [scale, layers]);

  // Get appropriate image for current zoom level
  const getLayerImage = (layer) => {
    if (scale >= HIGH_RES_THRESHOLD && layer.isHighResLoaded) {
      return layer.highResImage;
    }
    return layer.lowResImage; // Fall back to low-res
  };

  // Check if layer should use high-res
  const shouldUseHighRes = (layer) => {
    return scale >= HIGH_RES_THRESHOLD && layer.isHighResLoaded;
  };

  // Event handlers
  const handleMouseDown = useCallback((e) => {
    if (e.button === 0) {
      e.preventDefault();
      isDragging.current = true;
      dragStart.current = {
        x: e.clientX - position.x,
        y: e.clientY - position.y
      };
      lastPosition.current = { ...position };
      containerRef.current.style.cursor = 'grabbing';
    }
  }, [position]);

  const handleMouseMove = useCallback((e) => {
    if (!isDragging.current) return;

    e.preventDefault();
    const newX = e.clientX - dragStart.current.x;
    const newY = e.clientY - dragStart.current.y;

    if (Math.abs(newX - lastPosition.current.x) > 2 || Math.abs(newY - lastPosition.current.y) > 2) {
      setPosition({ x: newX, y: newY });
      lastPosition.current = { x: newX, y: newY };
    }
  }, []);

  const handleMouseUp = useCallback(() => {
    isDragging.current = false;
    if (containerRef.current) {
      containerRef.current.style.cursor = 'grab';
    }
  }, []);

  // UPDATED: resetZoom function - properly centers content both horizontally and vertically
  const resetZoom = useCallback(() => {
    setScale(1);
    
    // Center the content properly both horizontally and vertically
    if (containerRef.current && layers.length > 0 && layers[0].naturalSize) {
      const rect = containerRef.current.getBoundingClientRect();
      const firstLayer = layers[0];
      
      // Calculate center position for both x and y axes
      const centerX = (rect.width - firstLayer.naturalSize.width) /10;
      const centerY = (rect.height - firstLayer.naturalSize.height) / 200;
      
      setPosition({ x: centerX, y: centerY });
    }
  }, [layers]); // Add layers as dependency

  // Set up event listeners
  useEffect(() => {
    const container = containerRef.current;
    if (!container) return;

    const options = { passive: false };

    container.addEventListener('wheel', handleWheel, options);
    container.addEventListener('mousedown', handleMouseDown, options);
    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);

    return () => {
      container.removeEventListener('wheel', handleWheel);
      container.removeEventListener('mousedown', handleMouseDown);
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    };
  }, [handleWheel, handleMouseDown, handleMouseMove, handleMouseUp]);

  // FIXED: Layer management - no side effects on position
  const toggleLayer = useCallback((layerId) => {
    setLayers(prev => prev.map(layer =>
      layer.id === layerId ? { ...layer, visible: !layer.visible } : layer
    ));
    // Position remains unchanged when toggling layers
  }, []); // No dependencies that would cause re-centering

  const changeOpacity = useCallback((layerId, opacity) => {
    setLayers(prev => prev.map(layer =>
      layer.id === layerId ? { ...layer, opacity } : layer
    ));
  }, []);

  const resetAll = useCallback(() => {
    setLayers(prev => prev.map(layer => ({
      ...layer,
      opacity: 1,
      visible: layer.id === 1
    })));
    resetZoom();
  }, [resetZoom]);

  const enableAll = useCallback(() => {
    setLayers(prev => prev.map(layer => ({
      ...layer,
      opacity: 1,
      visible: true
    })));
    resetZoom();
  }, [resetZoom]);

  const disableAll = useCallback(() => {
    setLayers(prev => prev.map(layer => ({
      ...layer,
      opacity: 1,
      visible: false
    })));
    resetZoom();
  }, [resetZoom]);

  const reloadLayer = useCallback(async (layerId) => {
    const layer = layers.find(l => l.id === layerId);
    if (!layer) return;

    try {
      const img = new Image();
      await new Promise((resolve, reject) => {
        img.onload = resolve;
        img.onerror = () => reject(new Error(`Failed to load ${layer.lowResPath}`));
        img.src = layer.lowResPath;
      });

      setLayers(prev => prev.map(l =>
        l.id === layerId ? {
          ...l,
          lowResImage: img,
          highResImage: null,
          isHighResLoaded: false,
          error: null,
          naturalSize: {
            width: img.naturalWidth,
            height: img.naturalHeight
          }
        } : l
      ));
    } catch (error) {
      setLayers(prev => prev.map(l =>
        l.id === layerId ? { ...l, error: error.message } : l
      ));
    }
  }, [layers]);

  // Memory usage indicator
  const memoryUsage = (highResLayers.size / layers.length) * 100;

  if (isLoading) {
    return (
      <div className="app">
        <div className="loading">Loading Images...</div>
      </div>
    );
  }

  return (
    <div className="app">
      {/* Simple Resolution Modal */}
      {showModal && (
        <div className="resolution-modal-overlay">
          <div className="resolution-modal">
            <div className="modal-content">
              <FontAwesomeIcon icon={faSpinner} spin className="modal-icon" />
              <span className="modal-text">{modalText}</span>
            </div>
          </div>
        </div>
      )}

      {/* Header */}
      <header className="app-header">
        <div className="header-content">
          <div className="header-title">
            <h1>Monosynaptic Rabies Input to the Inferior Olive</h1>
          </div>
          <div className="header-info">
            <span className="case-count">
              <FontAwesomeIcon icon={faLayerGroup} /> {layers.length} Active Layers
            </span>
          </div>
        </div>
      </header>

      {/* Image Display Area */}
      <div className="app-main">
        <div
          className="image-container"
          ref={containerRef}
          style={{ cursor: isDragging.current ? 'grabbing' : 'grab' }}
        >
          <div
            className="image-transform-container"
            style={{
              transform: `translate(${position.x}px, ${position.y}px) scale(${scale})`,
              transformOrigin: '0 0'
            }}
          >
            {layers.map(layer => {
              const image = getLayerImage(layer);
              return (
                <div
                  key={layer.id}
                  className="image-layer"
                  style={{
                    opacity: layer.visible ? layer.opacity : 0,
                  }}
                >
                  {image ? (
                    <img
                      src={image.src}
                      alt={layer.name}
                      className="layer-image"
                      style={{
                        width: '100%',
                        height: '100%',
                        objectFit: 'contain'
                      }}
                    />
                  ) : layer.error ? (
                    <div className="image-error">
                      <div>❌ Failed to load image</div>
                      <div className="error-path">{layer.lowResPath}</div>
                      <button onClick={() => reloadLayer(layer.id)}>
                        Retry Loading
                      </button>
                    </div>
                  ) : (
                    <div className="image-loading">Loading {layer.name}...</div>
                  )}
                </div>
              );
            })}
          </div>

          {/* Performance Info */}
          <div className="performance-info">
            <div className="resolution-info">
              {scale >= HIGH_RES_THRESHOLD ? 'High Resolution - Mode' : 'Low Resolution - Mode'}
            </div>
            <div className="memory-info">
              Memory: {highResLayers.size}/{layers.length} layers loaded ({Math.round(memoryUsage)}%)
            </div>
            <div className="zoom-info-display">
              Zoom: {Math.round(scale * 100)}% {scale >= HIGH_RES_THRESHOLD && '🎯'}
            </div>
          </div>

          {/* Zoom Controls */}
          <div className="zoom-controls">
            <button onClick={handleZoomIn}>
              <FontAwesomeIcon icon={faMagnifyingGlassPlus} /> Zoom In
            </button>
            <button onClick={resetZoom}>
              <FontAwesomeIcon icon={faArrowsToCircle} /> Reset
            </button>
            <button onClick={handleZoomOut}>
              <FontAwesomeIcon icon={faMagnifyingGlassMinus} /> Zoom Out
            </button>
            <div className="zoom-level">
              {Math.round(scale * 100)}%
            </div>
          </div>
        </div>

        <div className="controls">
          {/* Viewer Info */}
          <div className='sticky-headers'>
            <div className="info-panel">
              <div className="info-header">
                <FontAwesomeIcon icon={faCircleInfo} />
                <h3>Viewer Information</h3>
              </div>
              <div className="info-content">
                <div className="info-item">
                  <FontAwesomeIcon icon={faMagnifyingGlassMinus} />
                  <span><strong>Low Res</strong> - Faster loading, optimizing performance</span>
                </div>
                <div className="info-item">
                  <FontAwesomeIcon icon={faMagnifyingGlassPlus} />
                  <span><strong>High Res</strong> - Full detail when zoomed in</span>
                </div>
                <div className="info-item">
                  <FontAwesomeIcon icon={faWandMagicSparkles} />
                  <span><strong>Dynamic Memory</strong> - Auto-manage resources</span>
                </div>
              </div>
            </div>
            
            <div className="controls-header">
              <h2>
                <FontAwesomeIcon icon={faSliders} />
                Layer Controls
              </h2>
            </div>
            
            {/* Action Buttons */}
            <div className="action-buttons">
              <button onClick={enableAll} className="action-btn reset-all-btn">
                <FontAwesomeIcon icon={faCheckDouble} /> Enable All
              </button>
              <button onClick={resetAll} className="action-btn reset-all-btn">
                <FontAwesomeIcon icon={faX} /> Reset All
              </button>
            </div>
            <div className='action-buttons'>
              <button onClick={resetZoom} className="action-btn reset-zoom-btn">
                <FontAwesomeIcon icon={faArrowsToCircle} /> Center View
              </button>
            </div>
          </div>
          <div className='scrollable-controls'>
            {/* Layer Controls */}
            <div className="layer-controls">
              {layers.map(layer => (
                <div key={layer.id} className={`layer-control ${shouldUseHighRes(layer) ? 'high-res-active' : ''}`}>
                  <div className="layer-header">
                    <label>
                      <input
                        type="checkbox"
                        checked={layer.visible}
                        onChange={() => toggleLayer(layer.id)}
                      />
                      <span className="layer-name">{layer.name}</span>
                    </label>
                  </div>

                  <div className="layer-info">
                    <small className="resolution-info">
                      {shouldUseHighRes(layer) ? 'High Resolution' : 'Low Resolution'}
                      {layer.isHighResLoaded && ' (Cached)'}
                    </small>
                    {layer.naturalSize && (
                      <small className="image-size">
                        {layer.naturalSize.width} × {layer.naturalSize.height}px
                      </small>
                    )}
                  </div>

                  {layer.error && (
                    <div className="error-message">
                      <span>❌</span>
                      <span>Failed to load</span>
                      <button onClick={() => reloadLayer(layer.id)}>
                        Retry
                      </button>
                    </div>
                  )}

                  {!layer.error && (
                    <div className="opacity-control">
                      <input
                        type="range"
                        min="0"
                        max="1"
                        step="0.1"
                        value={layer.opacity}
                        onChange={(e) => changeOpacity(layer.id, parseFloat(e.target.value))}
                        disabled={!layer.visible}
                        className="opacity-slider"
                      />
                      <span className="opacity-value">
                        {Math.round(layer.opacity * 100)}%
                      </span>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Footer */}
      <footer className="app-footer">
        <div className="footer-content">
          <div className="footer-info">
            <span>SVG/PNG Viewer • Built with React</span>
          </div>
          <div className="footer-stats">
            <div className="footer-item">
              © UCLA Brain Research &amp; Artificial Intelligence Nexus
            </div>
            <div>
              <span>
                <a href="https://uclabrain.org/">
                  <img src="static/BRAIN_logo.png" width="84px" height="72px" className="img-fluid" id="brainlogo"/>
                </a>
              </span>
            </div>        
          </div>
        </div>
      </footer>
    </div>
  );
}

export default App;